import React from 'react';
import ContainerTitle from '../components/ui/ContainerTitle';
import { withTitle } from '../Context';
import Badge from '../components/ui/Badge';

const About = () => (
    <ContainerTitle title="About me">
        <p className="lead text-justify">
            Je m'appelle <Badge>Jean-Charles Mouss</Badge>, j'ai <Badge>20 ans</Badge>, et je suis un grand passionn (un peu trop) d'informatique et plus particulirement du <Badge>developpement</Badge>.
            J'ai suivi une formation de <Badge>DUT Informatique</Badge>  l'IUT de Laval (53), cela m'a permis directement aprs le BAC de pouvoir exercer et en <Badge>apprendre</Badge> d'avantages sur la programmation.
            Car bien avant le DUT, j'apprenais en <Badge>autodidacte</Badge> le dveloppement, que ce soit des <Badge>sites internet</Badge> ou encore des applications <Badge>Android</Badge>.
        </p>
        <p className="lead text-justify">
            Je poursuis mes tudes avec une formation de <Badge>Concepteur Dveloppeur</Badge> spcialit Mobile  Efreitech, une cole situ  Villejuif (94), en <Badge>alternance</Badge> sur 2 ans.
            Cette formation m'attire particulirement car elle offre une formation en alternance et propose des cours trs centrs sur le dveloppement <Badge>mobile</Badge> et <Badge>web</Badge>.
        </p>
        <p className="lead text-justify">
            J'adore apprendre de nouvelles <Badge>technologies</Badge>, afin de rester en <Badge>veille permanente</Badge>, ce qui est trs important dans le monde de l'informatique, car tout <Badge>volue</Badge> trs rapidement !
        </p>
    </ContainerTitle>
);

export default withTitle(About, "About");


